	module.exports = {"data":{"markdownRemark":{"html":"<p>Mark asserts that while using onclick is frowned upon, it's faster and cleaner:</p>\n<blockquote>\n<p>Sure it makes your XHTML a bit longer, but that’s one less selector jQuery has to parse and one less DOMReady function\nyour browser has to kick off.</p>\n</blockquote>\n<p>That's <em>true</em>, but it comes at the cost of maintainability. Putting the jQuery example he provided into an external file makes it easier to maintain by having all the JavaScript in predictable and findable places.</p>\n<p>Additionally, that performance hit can be mitigated by using progressive enhancement to give the entire page functionality immediately, then asynchronously loading the JavaScript to enhance it. Asynchronously loading your JS may or may not even be necessary, depending on your JS payload.</p>\n<p>Using inline code like this isn't necessary for most people; they just make your code harder to read.</p>","fields":{"post":{"title":"onclick is Your Friend","link":"http://thenerdary.net/articles/entry/onclick-is-your-friend","timestamp":"2010-06-21T23:00Z","date":"June 21, 2010 11:00pm UTC"}}}},"pathContext":{"slug":"/onclick-is-your-friend.html"}}

